.postsBlock {
    padding: 10px;
}

.posts {
    margin-top: 10px;
    
}

.photo {
    width: 50px;
}

.post-enter {
    transform: translateX(0px);
}
.post-enter-active {
  transform: translateX(350px);
  transition: all 500ms ease-in;
}
.post-exit {
  opacity: 1;
}
.post-exit-active {
  transform: translateX(-350px);
  transition: all 500ms ease-in;
}